.navbar{
    margin: auto;
    padding: 2rem;
    display: inline-flex;
    position: relative;
    // position: absolute;
    // top: 0;
    // left: 0;
}

.navcontainer{
    padding: 2rem;
    padding-left: 0;
}

.navbutton{
    color: white;
    font-size: 4rem;
}

.navboarder{
    border: 0.5vmin;
    border-style: solid;
    border-radius: 2vmin;
    padding: 1.25vmin;
}

@media only screen and (max-width: 1000px) {
    .navbar{
        margin: auto;
        padding: 2rem;
        display: inline-flex;
        // position: absolute;
        // top: 0;
        // left: 0;
    }
    
    .navcontainer{
        padding: 2rem;
        padding-left: 0;
    }
    
    .navbutton{
        color: white;
        font-size: 3rem;
    }
    
    .navboarder{
        border: 0.5vmin;
        border-style: solid;
        border-radius: 2vmin;
        padding: 1.25vmin;
    }
}